.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(to right, #ffe6e6, #ffc6c6);
  color: #5a5a5a;
}

.header-wrapper {
  margin-bottom: 2rem;
}

.header-content {
  margin-bottom: 2rem;
}

.header-wrapper h1 {
  font-size: 2.5rem;
  color: #b30000;
  margin-bottom: 0.5rem;
  position: relative;
}

.header-wrapper h1::before,
.header-wrapper h1::after {
  content: "ðŸ©µðŸ‘‘";
  font-size: 1.5rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.header-wrapper h1::before {
  left: -4rem;
}

.header-wrapper h1::after {
  right: -4rem;
}

.header-wrapper h2 {
  font-size: 1.5rem;
  color: #ff6666;
  margin: 0;
}

.is-christmas-wrapper,
.countdown-wrapper {
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  border-radius: 1rem;
  padding: 2rem;
}

button {
  background: #ff6666;
  border: 0;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  color: #fff;
  font-weight: 700;
  cursor: pointer;
  transition: all 200ms ease-in-out;
  text-transform: uppercase;
  font-size: 1rem;
}

button:hover {
  filter: opacity(0.8);
}

